<template>
  <component :is="headingLevel" v-html="text"></component>
</template>

<script>
export default {
  props: {
    text: {
      type: String,
      required: true
    },
    level: {
      type: Number,
      default: 2
    }
  },
  computed: {
    headingLevel() {
      return `h${this.level}`;
    },
  },
}
</script>

<style lang="scss">
  h1 {
    font-family: 'trade-std-obl', 'Helvetica', sans-serif;
    font-size: 22px;
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.5;
    letter-spacing: normal;
    color: #222222;
    margin: 0;
  }

  h2 {
    color: #222222;
    font-family: 'trade-std-obl-bold', 'Helvetica', sans-serif;
    font-size: 32px;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.5;
    letter-spacing: normal;
    text-align: center;
    text-transform: uppercase;
  }

  h3 {
    font-family: 'trade-std-obl-lite', 'Helvetica', sans-serif;
    margin: 0;
    font-size: 18px;
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.83;
    letter-spacing: normal;
    text-align: center;

    span {
      &.bold {
        font-family: 'trade-std-obl-bold', 'Helvetica', sans-serif;
      }
    }
  }

  h4 {
    font-family: 'trade-std-bold', 'Helvetica', sans-serif;
    font-size: 16px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.56;
    letter-spacing: 1.3px;
    color: #222222;
    text-transform: uppercase;
  }
</style>




