<template>
  <div id="main-menu">
    <input id="main-menu__toggle" type="checkbox" />
    <span></span>
    <span></span>
    <span></span>
    <div id="menu">
      <ul class="top-menu">
        <li><a href="#">about the project</a></li>
        <li><a href="#">our mission</a></li>
        <li><a href="#">world wildlife fund</a></li>
        <li><a href="#">mission impawssible app</a></li>
        <li><a href="#">tigers in vr</a></li>
        <li>
          <a href="#">share</a>
          <img class="share-icon share-twitter" src="../assets/icons/twitter.svg" alt="Share on Twitter">
          <img class="share-icon share-facebook" src="../assets/icons/facebook.svg" alt="Share on Twitter"> 
        </li>
      </ul>
      <ul class="sub-menu">
        <li><a href="#">tigerpedia</a></li>
        <li><a href="#">adopt a tiger</a></li>
        <li><a href="#">donate</a></li>
        <li><a href="#">take action</a></li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {

}
</script>

<style lang="scss" scoped>
@import '../assets/scss/import-all';

#main-menu {
  display: inline-block;
  position: relative;
  top: 30px;
  left: 30px;
  z-index: 1;
  -webkit-user-select: none;
  user-select: none;

  &__toggle {
    display: block;
    width: 40px;
    height: 32px;
    position: absolute;
    top: -7px;
    left: -5px;
    cursor: pointer;
    opacity: 0; /* hide this */
    z-index: 2; /* and place it over the hamburger */
    -webkit-touch-callout: none;
  }

  span {
    display: block;
    width: 33px;
    height: 4px;
    margin-bottom: 5px;
    position: relative;
    background: #000;
    border-radius: 3px;
    z-index: 1;
    transform-origin: 4px 0px;
    transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
      background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
      opacity 0.55s ease;

    &:first-child {
      transform-origin: 0% 0%;
    }

    &:nth-last-child(2) {
      transform-origin: 0% 100%;
    }
  }
}

/* 
 * Transform all the slices of hamburger
 * into a crossmark.
 */
#main-menu input:checked ~ span
{
  opacity: 1;
  transform: rotate(45deg) translate(-2px, -1px);
  background: #fff;
}

/*
 * But let's hide the middle one.
 */
#main-menu input:checked ~ span:nth-last-child(3)
{
  opacity: 0;
  transform: rotate(0deg) scale(0.2, 0.2);
}

/*
 * Ohyeah and the last one should go the other direction
 */
#main-menu input:checked ~ span:nth-last-child(2)
{
  transform: rotate(-45deg) translate(0, -1px);
}

#menu {
  box-sizing: border-box;
  position: absolute;
  width: 400px;
  margin: -100px 0 0 -50px;
  padding: 50px;
  padding-top: 125px;
  background-color: rgba(236, 99, 33, 0.95);
  list-style-type: none;
  -webkit-font-smoothing: antialiased;
  transform-origin: 0% 0%;
  transform: translate(-100%, 0);
  transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);

  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;

    &.top-menu {
      border-bottom: 1px solid #fff;
      padding-bottom: 25px;

      li {
        font-family: 'trade-std-obl', 'Helvetica', sans-serif;
        font-size: 20px;
        font-weight: bold;
        font-style: normal;
        font-stretch: normal;
        letter-spacing: 1.9px;
      }

      .share-icon {
        width: 40px;
        margin: 0 20px -14px 20px;

        &:nth-of-type(2) {
          margin: 0 20px -14px 0;
        }
      }
    }

    &.sub-menu {
      padding-top: 25px;

      li {
        font-family: 'trade-std', 'Helvetica', sans-serif;
        font-size: 16px;
      }
    }

    li {
      padding: 18px 0;
      text-transform: uppercase;

      a {
        color: #fff;
        text-decoration: none;
      }
    }
  }
}

#main-menu input:checked ~ #menu
{
  transform: none;
}
</style>



