<template>
  <p class="paragraph" v-html="text"></p>
</template>

<script>
export default {
  props: {
    text: String
  }
}
</script>

<style lang="scss">
  @import '../assets/scss/import-all';

  .paragraph {
    font-family: 'Helvetica', sans-serif;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.57;
    letter-spacing: 0.8px;
    color: #222222;

    a {
      color: $tiger-orange;
      text-decoration: none;
    }
  }
</style>



