<template>
  <section class="page-content">
    <headline
      :level="3"
      text="There are about 5,000 captive tigers in the United States alone, but there <span class='bold'>are fewer than 3,200 tigers in the wild</span>."
    />
    <project-logos/>
    <headline
      :level="2"
      text="mission impawssible"
    />
    <headline
      :level="4"
      text="making a pawsitive impact one step at a time"
    />
    <paragraph text="Mission ImPAWssible is a step tracking app with all proceeds going towards Discoverys Project C.A.T. (Conserving Acres for Tigers) and the WWF. Discovery has long been committed to telling the stories and protecting the creatures that make our planet extraordinary, and Project C.A.T.s mission is to ensure a future for tigers and other endangered wildlife so there will always be stories to tell. To learn more about Project C.A.T. <a href=''>click here</a>."/>
  </section>
</template>

<script>
import Headline from './headline.vue';
import Paragraph from './paragraph.vue';
import ProjectLogos from './project-logos.vue';

export default {
  components: {
    Headline,
    Paragraph,
    ProjectLogos
  }
}
</script>

<style>
.page-content {
  padding: 0 20px;
}
</style>



